import React, { Component } from "react";
import { Container, Row, Col, Card, CardImg, CardText, CardBody, CardTitle, CardSubtitle, Button } from "reactstrap";
import teamImg from '../../assets/img/318x180.svg'


class Team extends Component {
    componentDidMount() {
        setTimeout(() => {
            window.scrollTo(0, 0)
        }, 500)
    }

    render() {
        return (
            <section id="team">
                <div className="cover-wrapper">
                    <Container>
                        <div className="cover-title">
                            Team
                        </div>
                    </Container>
                </div>

                <Container className="section-padding">
                    <Row>
                        <Col>
                            <Card>
                                <CardImg top width="100%" src={teamImg} alt="Card image cap" />
                                <CardBody>
                                    <CardTitle tag="h5">Mr. Jon</CardTitle>
                                    <CardSubtitle tag="h6" className="mb-2 text-muted">Managing Director</CardSubtitle>
                                    <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                                    <Button color="dark" className="mt-3">View More...</Button>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <CardImg top width="100%" src={teamImg} alt="Card image cap" />
                                <CardBody>
                                    <CardTitle tag="h5">Mr. Jon</CardTitle>
                                    <CardSubtitle tag="h6" className="mb-2 text-muted">Managing Director</CardSubtitle>
                                    <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                                    <Button color="dark" className="mt-3">View More...</Button>
                                </CardBody>
                            </Card>
                        </Col>
                        <Col>
                            <Card>
                                <CardImg top width="100%" src={teamImg} alt="Card image cap" />
                                <CardBody>
                                    <CardTitle tag="h5">Mr. Jon</CardTitle>
                                    <CardSubtitle tag="h6" className="mb-2 text-muted">Managing Director</CardSubtitle>
                                    <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                                    <Button color="dark" className="mt-3">View More...</Button>
                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </Container>
            </section>
        );
    }
}

export default Team;
