import React from "react";
import { Modal } from "react-bootstrap";
import { Button, ModalFooter, Table } from "reactstrap";
import PropTypes from 'prop-types'
import { faPrint } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";



const OrderDetailModal = ({ modalIsOpen, modalHandler, selectedOrder: { id, order_date, delivery_date, total_amount, status, order_items } }) => {
    return (
        <Modal
            size="xl"
            dialogClassName="modal-90w"
            show={modalIsOpen}
            backdrop="static"
            scrollable={true}
            onHide={() => modalHandler()}
        >
            <Modal.Header closeButton>
                <Modal.Title>Order Detail</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">Order ID</th>
                            <th>Order Date</th>
                            <th>Delivery Date</th>
                            <th>Total Amount</th>
                            <th>Status</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr key={id}>
                            <td>{id}</td>
                            <td>{order_date}</td>
                            <td>{delivery_date}</td>
                            <td>{total_amount}</td>
                            <td>{status}</td>
                            <td>
                                <Button color="dark" size="sm">
                                    <FontAwesomeIcon icon={faPrint} />
                                </Button>
                            </td>
                        </tr>
                    </tbody>
                </Table>

                <hr />
                <h5>Order Items</h5>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">ID</th>
                            <th>Code</th>
                            <th>Product Name</th>
                            <th>Unit</th>
                            <th>Rate (TK)</th>
                            <th>Quantity</th>
                            <th>Total (TK)</th>
                        </tr>
                    </thead>
                    <tbody>
                        {order_items && order_items.length > 0 && order_items.map(item => <tr key={item.id}>
                            <td>{item.id}</td>
                            <td>{item.product.product_code}</td>
                            <td>{item.product.name}</td>
                            <td>{item.unit}</td>
                            <td>{item.price}</td>
                            <td>{item.quantity}</td>
                            <td>{item.amount}</td>
                        </tr>)}
                    </tbody>
                </Table>
            </Modal.Body>
            <ModalFooter>
                <Button color="secondary" onClick={() => modalHandler()}>Close</Button>
            </ModalFooter>
        </Modal>
    )
}


OrderDetailModal.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    selectedOrder: PropTypes.object.isRequired,
    modalIsOpen: PropTypes.bool,
}

export default OrderDetailModal;
