import React, { Component } from 'react'
import { Table, Button } from 'reactstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faPenAlt, faCheck, faTimes } from "@fortawesome/free-solid-svg-icons";
import OrderDetailModal from './OrderDetailModal';
import PropTypes from 'prop-types'


class OrderHistory extends Component {
    state = {
        selectedOrder: {},
        modalIsOpen: false
    }


    render() {
        let { orders, isLoading, editHandler, orderApproveCancelHandler } = this.props
        let { selectedOrder, modalIsOpen } = this.state

        return (
            <>
                <Table responsive striped bordered hover size="sm" className="text-center">
                    <thead>
                        <tr>
                            <th width="15">ID</th>
                            <th>Order Date</th>
                            <th>Delivery Date</th>
                            <th>Total Items</th>
                            <th>Total Amount</th>
                            <th>Status</th>
                            <th width="10">Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {isLoading ? <tr>
                            <td colSpan={10}>Loading...</td>
                        </tr> : orders.length > 0 && orders.map(item => <tr key={item.id}>
                            <td>{item.id}</td>
                            <td>{item.order_date}</td>
                            <td>{item.delivery_date}</td>
                            <td>{item.order_items.length || 0}</td>
                            <td>{item.total_amount}</td>
                            <td>{item.status}</td>
                            <td>
                                {(item.status === 'Proposed' || item.status === 'Pending') &&
                                    <Button color="danger" size="sm" onClick={() => window.confirm('Are you sure?') && orderApproveCancelHandler(item.id, 'cancel')}>
                                        <FontAwesomeIcon icon={faTimes} />
                                    </Button>
                                }

                                {item.status === 'Pending' &&
                                    <Button color="primary" size="sm" onClick={() => window.confirm('Are you sure?') && orderApproveCancelHandler(item.id, 'confirmed')}>
                                        <FontAwesomeIcon icon={faCheck} />
                                    </Button>
                                }

                                {item.status === 'Proposed' &&
                                    <Button color="dark" size="sm" onClick={() => editHandler(item)}>
                                        <FontAwesomeIcon icon={faPenAlt} />
                                    </Button>
                                }

                                <Button color="warning" size="sm" onClick={() => this.setState({ selectedOrder: item, modalIsOpen: true })}>
                                    <FontAwesomeIcon icon={faEye} />
                                </Button>
                            </td>
                        </tr>)}
                        {!isLoading && orders.length === 0 &&
                            <tr><td colSpan={10}>There is no item in the list.</td></tr>}
                    </tbody>
                </Table>
                <OrderDetailModal
                    modalIsOpen={modalIsOpen}
                    selectedOrder={selectedOrder}
                    modalHandler={() => this.setState({ selectedOrder: {}, modalIsOpen: false })}
                />
            </>
        )
    }
}


OrderHistory.propTypes = {
    order: PropTypes.array,
    editHandler: PropTypes.func.isRequired,
    orderApproveCancelHandler: PropTypes.func.isRequired,
}

export default OrderHistory