import React, { Component } from "react";
import { Container } from "reactstrap";


class Contact extends Component {

    componentDidMount() {
        setTimeout(() => {
            window.scrollTo(0, 0)
        }, 500)
    }

    render() {
        return (
            <section id="contact">
                <div className="cover-wrapper">
                    <Container>
                        <div className="cover-title">
                            Contact
                        </div>
                    </Container>
                </div>

                <Container className="section-padding">
                    <p className="p">
                        Arrot’s growers range in size from 10 to over 10,000 decimals lands
                        of vegetables, and can be found farming all along the Northern part
                        and Southern part of Bangladesh. We are developing 10 clusters of
                        vegetables all over Bangladesh. They grow the best organic produce
                        available in the market. Arrot’s carefully hand selects expert
                        growers who hold the same high standards and values to promote
                        mutual success. The Arrot brand is passionate about purchasing good
                        food from good people; our local farms represent the best in the
                        industry so we strive to nurture them.
                    </p>
                </Container>
            </section>
        );
    }
}

export default Contact;
