import React from 'react'
import { Container } from 'reactstrap'
import bgImg from '../../assets/img/slider/imageedit_29_5127185664.jpg'
import bgImg2 from '../../assets/img/slider/imageedit_31_5200351415.jpg'

const Promote = () => {
    return (
        <section id='promote-wrapper' className='clearfix section-padding'>
            <Container>
                <div className="box">
                    <img src={bgImg} alt='slide' />
                    <h4 className='pt-3'>Promote Organics</h4>
                    <p>
                        Arrot is an organic produce brand owned and operated by Arrot Krishi Ponno Limited. At Arrot we
                        are dedicated to supply quality vegetables free from harmful chemicals to our customers by
                        building strong, lasting relationships with growers who are stewards of the land and their
                        communities. Growers who pack in the Arrot Brand meet our strict criteria for excellence and
                        consistency.

                        <br />

                        This relationship provides our growers with a reliable customer for their crops, and allows
                        Arrot’s Products a consistent supply of quality produce for distribution. We are proud to
                        support an infrastructure that keeps farming viable for families and communities for years to
                        come, and to be a partner in supplying quality organic produce to our valued customers.
                    </p>
                </div>
                <div className="box">
                    <img src={bgImg2} alt='slide' />
                    <h4 className='pt-3'>A Food Brand Exploring</h4>
                    <p>
                        With the establishment of a grower liaison, we were able to create a system that maintains the
                        benefit to the growers and to the consumer alike. In the beginning we had 12 growers packing
                        without any Brand and today we have over 30. These efforts have encouraged people to try farming
                        to provide a solid income for growers who sell to us on a regular basis.

                        In addition to our staples like potatoes and onions we have many other unique organic items.
                        Whether it is Eggplants, Cauliflower, Sweet Pumpkin, Flat Beans (shim), Bottlegourd (Lau) or
                        waterpumpkins, we are the matchmaker between the customer and the grower.
                    </p>
                </div>
            </Container>
        </section>
    )
}
export default Promote;
