import { postRequest, getRequest, putRequest } from '.'
import setAlertMessage from '../utils/setAlertMessage'



class Product {


    // GET PRODUCT LIST
    async get() {
        return await getRequest('/orders/')
    }


    // ORDER PLACE
    async submitOrder(formData) {
        let response = await postRequest('/orders/', formData)
        if (!response.isSuccess) {
            setAlertMessage('Something went wrong!', 'error')
        } else {
            setAlertMessage('Your order has been successfully submitted!', 'success')
        }
        return response
    }



    // UPDATE ORDER
    async updateOrder(id, formData) {
        let response = await putRequest(`/orders/${id}/`, formData)
        if (!response.isSuccess) {
            setAlertMessage('Something went wrong!', 'error')
        } else {
            setAlertMessage('Your order has been updated successfully!', 'success')
        }
        return response
    }


    // ORDER CANCELED OR CONFIRMED
    async orderCancelConfirmed(id, status) {
        let response = await putRequest(`/orders/${id}/${status}/`)
        if (!response.isSuccess) {
            setAlertMessage('Something went wrong!', 'error')
        } else {
            setAlertMessage(`Your order has been ${status === 'confirmed' ? 'confirmed' : 'canceled'} successfully!`, 'success')
        }
        return response
    }



    // SEARCH ORDER 
    async searchOrder(url) {
        return await getRequest(url)
    }





}
export default Product