import React, { useState } from "react";
import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption,
} from "reactstrap";
import PropTypes from "prop-types";
import bgImg1 from "../../assets/img/slider/imageedit_33_8661753618.jpg";
import bgImg2 from "../../assets/img/slider/imageedit_23_3827947359.jpg";
import bgImg3 from "../../assets/img/slider/imageedit_29_5127185664.jpg";

const items = [
    {
        src: bgImg1,
        altText: "Slide 1",
        caption: "Better Food, Better Life.",
    },
    {
        src: bgImg2,
        altText: "Slide 2",
        caption: "Arrot Brings Vegetables From The Field To Your Kitchen.",
    },
    {
        src: bgImg3,
        altText: "Slide 3",
        caption: "Arrot Ensures Standards Of All Vegitables.",
    },
];

const Banner = ({ history }) => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);

    const next = () => {
        if (animating) return;
        const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
        setActiveIndex(nextIndex);
    };

    const previous = () => {
        if (animating) return;
        const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
        setActiveIndex(nextIndex);
    };

    const goToIndex = (newIndex) => {
        if (animating) return;
        setActiveIndex(newIndex);
    };

    const slides = items.map((item) => {
        return (
            <CarouselItem
                onExiting={() => setAnimating(true)}
                onExited={() => setAnimating(false)}
                key={item.src}
            >
                <img src={item.src} alt={item.altText} />
                <CarouselCaption
                    captionText=''
                    captionHeader={item.caption}
                />
            </CarouselItem>
        );
    });

    return (
        <section id="banner">
            <Carousel activeIndex={activeIndex} next={next} previous={previous}>
                <CarouselIndicators
                    items={items}
                    activeIndex={activeIndex}
                    onClickHandler={goToIndex}
                />
                {slides}
                <CarouselControl
                    direction="prev"
                    directionText="Previous"
                    onClickHandler={previous}
                />
                <CarouselControl
                    direction="next"
                    directionText="Next"
                    onClickHandler={next}
                />
            </Carousel>
        </section>
    );
};

Banner.propTypes = {
    history: PropTypes.object.isRequired,
};

export default Banner;
