import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import history from './utils/history';

// IMPORT COMPONENT
import Home from "./components/home";
import PageNotFound from "./pages/PageNotFound";
import TopHeader from './components/layout/TopHeader';
import Footer from './components/layout/Footer'
import Login from './components/authentication/Login'
import Order from "./components/order";
import About from './components/about';
import Growers from './components/growers';
import Contact from "./components/contact";
import Buyers from './components/buyers';
import Team from "./components/team";
import WhyArrot from "./components/why-arrot";
import { connect } from 'react-redux';




class Routes extends Component {
    state = {
        wheelPosition: {},
        loggedIn: false
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        if (nextProps.auth.loggedIn === prevState.loggedIn) return null
        return {
            loggedIn: nextProps.auth.loggedIn,
        }
    }


    render() {
        let { wheelPosition, loggedIn } = this.state

        return (
            <Router>
                <TopHeader history={history} wheelPosition={wheelPosition} />
                <main id="site-main" onWheel={(e) => this.setState({ wheelPosition: e })}>
                    <Switch>
                        {loggedIn ?
                            <Switch>
                                <Route path="/order" exact component={Order} history={history} />
                                <Route path="/" exact component={Home} history={history} />
                                <Route path="/*" exact component={Home} history={history} />
                            </Switch>
                            : <Switch>
                                <Route path="/about" exact component={About} history={history} />
                                <Route path="/growers" exact component={Growers} history={history} />
                                <Route path="/buyers" exact component={Buyers} history={history} />
                                <Route path="/why-arrot" exact component={WhyArrot} history={history} />
                                <Route path="/team" exact component={Team} history={history} />
                                <Route path="/contact" exact component={Contact} history={history} />
                                <Route path="/login" exact component={Login} history={history} />
                                <Route path="/" exact component={Home} history={history} />
                                <Route path="/*" exact component={Home} history={history} />
                            </Switch>
                        }
                    </Switch>
                </main>
                <Footer />
            </Router>
        )
    }
}


const mapStateToProps = state => ({
    auth: state.auth
})
export default connect(mapStateToProps)(Routes) 