import React, { Component } from 'react'
import { Row, Col, Button, Label } from 'reactstrap';
import { Form } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faFilter, faTrash } from "@fortawesome/free-solid-svg-icons";
import PropTypes from 'prop-types'
import { OrderAction } from '../../services'


let iniState = {
    status: '',
    order_from_date: '',
    order_to_date: '',
    delivery_from_date: '',
    delivery_to_date: ''
}
const orderAction = new OrderAction()
class Search extends Component {
    state = iniState

    changeHandler = e => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    submitHandler = async e => {
        e.preventDefault()
        this.props.setOrderToState(true, [])

        let { status, order_from_date, order_to_date, delivery_from_date, delivery_to_date } = this.state

        let url = `orders/search?status=${status}&order_from_date=${order_from_date}&order_to_date=${order_to_date}&delivery_from_date=${delivery_from_date}&delivery_to_date=${delivery_to_date}`


        let { data } = await orderAction.searchOrder(url)
        this.props.setOrderToState(false, data ? data.results : [])
    }

    resetHandler = () => {
        this.setState(iniState)
        this.props.getData()
    }


    render() {
        let { modalHandler } = this.props
        let { status, order_from_date, order_to_date, delivery_from_date, delivery_to_date } = this.state


        return (
            <Row className='mb-5'>
                <Col>
                    <Label for='status'>Status</Label>
                    <Form.Control
                        as="select"
                        name="status"
                        id='status'
                        onChange={this.changeHandler}
                        value={status}
                    >
                        <option value=''>Select One</option>
                        <option value='Proposed'>Proposed</option>
                        <option value='Pending'>Pending</option>
                        <option value='Canceled'>Canceled</option>
                        <option value='Confirmed'>Confirmed</option>
                        <option value='Processing'>Processing</option>
                        <option value='Shipping'>Shipping</option>
                        <option value='Delivered'>Delivered</option>
                    </Form.Control>
                </Col>
                <Col>
                    <Label for='order_from_date'>Order From Date</Label>
                    <Form.Control type="date" name="order_from_date" value={order_from_date} onChange={this.changeHandler} />
                </Col>
                <Col>
                    <Label for='order_to_date'>Order To Date</Label>
                    <Form.Control type="date" name="order_to_date" value={order_to_date} onChange={this.changeHandler} />
                </Col>
                <Col>
                    <Label for='delivery_from_date'>Delivery From Date</Label>
                    <Form.Control type="date" name="delivery_from_date" value={delivery_from_date} onChange={this.changeHandler} />
                </Col>
                <Col>
                    <Label for='delivery_to_date'>Delivery To Date</Label>
                    <Form.Control type="date" name="delivery_to_date" value={delivery_to_date} onChange={this.changeHandler} />
                </Col>
                <Col>
                    <div className="d-flex justify-content-between">
                        <span>
                            <Label for='submit' className='d-block'>&nbsp;</Label>
                            <Button color="primary" size="sm" type='submit' id='submit' onClick={this.submitHandler}>
                                <FontAwesomeIcon icon={faFilter} />
                            </Button>
                        </span>
                        <span>
                            <Label for='reset' className='d-block'>&nbsp;</Label>
                            <Button color="dark" size="sm" type='reset' id='reset' onClick={this.resetHandler}>
                                <FontAwesomeIcon icon={faTrash} />
                            </Button>
                        </span>
                    </div>
                </Col>
                <Col>
                    <Label for='add-new-btn' className='d-block'>&nbsp;</Label>
                    <Button color="warning" size="sm" onClick={() => modalHandler()} id='add-new-btn'>
                        <FontAwesomeIcon icon={faPlus} /> Place Order
                                </Button>
                </Col>
            </Row>
        )
    }
}


Search.propTypes = {
    modalHandler: PropTypes.func.isRequired,
    setOrderToState: PropTypes.func.isRequired,
}


export default Search