import { postRequest, getRequest } from '.'
import store from '../store';
import { SET_USER } from './types';
import setAlertMessage from '../utils/setAlertMessage';



class Authenticate {


    // LOGIN HANDLER
    async loginHandler(formData) {
        let { isSuccess, data } = await postRequest('/login/', formData)
        // SET STORE
        if (isSuccess) {
            store.dispatch({
                type: SET_USER,
                payload: {
                    token: data.token,
                    user: data.user
                }
            })
            setAlertMessage('Login Successfully.', 'success')
        } else {
            setAlertMessage('Invalid Credentials!!!', 'error')
        }
        return isSuccess
    }



    // REGISTER HANDLER
    async registerSubmitHandler(formData) {
        return await postRequest('/auth/register', formData)
    }


    // LOGOUT HANDLER
    async logoutHandler(history) {
        try {
            let response = await getRequest('/logout/')
            if (!response) {
                setAlertMessage('Something went wrong!!!.', 'error')
                return false;
            }
            localStorage.clear();
            setAlertMessage('Logout Successfully.', 'success')
            store.dispatch({
                type: SET_USER,
                payload: {
                    token: null
                }
            })

            history.push('/')
            window.location.reload()

            return true
        } catch (error) {
            console.log(error)
            return false;
        }
    }



}

export default Authenticate