import React from 'react'
import {Container} from 'reactstrap'
import bgImg from '../../assets/img/slider/600.jpg'

const HowItWork = () => {
    return (
        <section id='how-it-work' className='section-padding'>
            <Container>
                <h3 className="page-title left-border text-left">HOW IT WORKS</h3>
                <h6 className='h6'><b>A FARMING PHILOSOPHY</b></h6>
                <p>
                    Every season we begin by meeting with each of our partner growers to determine what they will be
                    growing for us to sell and for distribution. This commitment provides the growers with security that
                    they will have a market for their produce, and gives Arrot’s a steady supply of top quality organic
                    produce for our customers.

                    <br/>

                    Growers agree to pack in our label, and when their produce is ready to market, we pick up at the
                    farm directly, doing whatever we can to make their lives easier. Our buyers purchase from the
                    growers all season and work around any weather or growing issues. Our goal is to create long term
                    relationships that provide years of benefits for both growers and consumers alike.
                </p>
                <div className="card-img">
                    <img src={bgImg} alt='how to work' className=''/>
                </div>
            </Container>
        </section>
    )
}
export default HowItWork;
