import React from 'react'
import {Container} from 'reactstrap'
import bgImg from '../../assets/img/slider/imageedit_39_5373247728.jpg'

const WhoWeAre = () => {
    return (
        <section id='how-it-work' className='section-padding'>
            <Container>
                <h3 className="page-title left-border text-left">WHO WE ARE</h3>
                <p>
                    Arrot is an organic produce brand owned and operated by Arrot Krishi Ponno Limited. At Arrot we are
                    dedicated to supply quality vegetables free from harmful chemicals to our customers by building
                    strong, lasting relationships with growers who are stewards of the land and their communities.
                    Growers who pack in the Arrot Brand meet our strict criteria for excellence and consistency.
                    <br/>
                    This relationship provides our growers with a reliable customer for their crops, and allows Arrot’s
                    Products a consistent supply of quality produce for distribution. We are proud to support an
                    infrastructure that keeps farming viable for families and communities for years to come, and to be a
                    partner in supplying quality organic produce to our valued customers.
                </p>
                <div className="card-img">
                    <img src={bgImg} alt='how to work' className=''/>
                </div>
            </Container>
        </section>
    )
}
export default WhoWeAre;
